	dingdanbianhao : false,
	kehuxingming : false,
	shangpinmingcheng : false,
	jiage : false,
	dingdanshuliang : false,
	dingdanzongjia : false,
	dingdanshijian : false,
	dingdanxiangqing : false,
	yuangongzhanghao : false,
	yuangongxingming : false,
	sfsh : false,
	shhf : false,
      },
